.list:nth-child(1) { width: 100%; margin-bottom: 2rem; height: 20rem; }
.list:nth-child(1) .fstvname { width: 50%; margin: 0 auto; font-size: 2rem; margin-top: 2rem; }
.list:nth-child(1) .date {   font-size: 2.5rem; margin: 3rem 0; }
.list { position: relative;  width: 31.6%; height: 13rem; padding: 2rem; color: #fff; text-align: center; cursor: pointer;  margin-left: 0.6rem; margin-right: 0.6rem; margin-bottom: 1.4rem; box-shadow: 0 0 7px #999;  }
/* .list::after { content: ''; position: absolute; top: 0; bottom: 0; left: 0; right: 0; background-color: rgba(0, 0, 0, 0.5); z-index: 1; } */
.list:hover .fstvname { border-bottom: 2px solid #fff; padding-bottom: 0.8rem; }
.fstvname { font-weight: bold; transition: all 0.5s; font-size: 1.3rem; margin: 1rem 0; padding: 0 1rem; position: relative; z-index: 999;   }

.date { margin: 2rem 0; position: relative; z-index: 999; font-size: 1.2rem;  overflow: hidden; white-space: nowrap; text-overflow: ellipsis;}
.tel { margin-bottom: 0.6rem; position: relative; z-index: 999; }

