.wrap { text-align: center; width: 60rem; margin: 0 auto; }
.ing, .end {  margin-top: 3rem; background: orangered; color: #fff; display: inline-block; padding: 0.5rem; border-radius: 4rem; font-size: 0.8rem;  }
.end { background: #999; }
.title { text-align: center; margin-top: 1.3rem; font-size: 2rem; }
.info { display: flex; justify-content: center; margin-top: 1rem; color: #999; }
.infoLeft { margin-right: 2rem; }

.bottomDetail {padding: 3rem 0;  }
.img { width: 40rem; }
.exbox { display: flex; justify-content: space-between; border-bottom: 2px solid #666; }
.detailInfo { font-size: 1.6rem; font-weight: bold; margin-top: 3rem; padding-bottom: 1.1rem; text-align: left; }
.tel { margin-top: 3.3rem; }
.explanation { margin-top: 2rem; margin-bottom: 5rem; text-align: left; line-height: 2; }


